import { Component, Input, OnInit, HostListener } from '@angular/core';
import { Subject } from 'rxjs';
import { BsModalRef } from 'ngx-bootstrap/modal';

enum KEY_CODE {
    ENTER = 13
}

export interface IAlertBoxInputs {
    hideNoBtn?: boolean;
    yesBtnLabel?: string;
    noBtnLabel?: string;
    noBtnClasses?: string;
    yesBtnClasses?: string;
    enableEnter?: boolean;
    title: string;
    innerBody: string;
}

@Component({
    selector: 'polp-bs-alert-box',
    templateUrl: './alert-box.component.html',
    styleUrls: ['./alert-box.component.css']
})
export class AlertBoxComponent implements OnInit {

    @Input() hideNoBtn: boolean;
    @Input() yesBtnLabel: string;
    @Input() noBtnLabel: string;
    @Input() noBtnClasses: string;
    @Input() yesBtnClasses: string;

    @Input() enableEnter: boolean = false;

    @Input() title: string;
    @Input() innerBody: string;

    result: Subject<boolean> = new Subject();

    constructor(private readonly _bsModalRef: BsModalRef) {
    }

    ngOnInit(): void {
        this.hideNoBtn = this.hideNoBtn || false;
        this.yesBtnLabel = this.yesBtnLabel || 'Yes';
        this.noBtnLabel = this.noBtnLabel || 'No';

        this.yesBtnClasses = this.yesBtnClasses || 'btn-primary';
        this.noBtnClasses = this.noBtnClasses || 'btn-secondary';
    }

    @HostListener('window:keyup', ['$event'])
    keyEvent(event: KeyboardEvent) {
        if (this.enableEnter && event.keyCode === KEY_CODE.ENTER) {
            this.confirm();
        }
    }

    close() {
        this.result.next(false);
        this.result.complete();
        this._bsModalRef.hide();
    }

    confirm() {
        this.result.next(true);
        this.result.complete();
        this._bsModalRef.hide();
    }

}
