import { Injectable } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { AlertBoxComponent } from '../components/alert-box/alert-box.component';

@Injectable({
    providedIn: 'root'
})
export class CommonModalsService {

    constructor(private readonly _modalService: BsModalService) { }

    showConfirmAsync(title: string, innerBody: string, modalClasses: string = 'modal-dialog-centered') {
        const modalRef = this._modalService.show(AlertBoxComponent, {
            animated: true,
            keyboard: false,
            class: modalClasses,
            initialState: {
                enableEnter: true,
                title: title,
                innerBody: innerBody
            },
            ignoreBackdropClick: true
        });

        return modalRef.content.result.toPromise() as Promise<boolean>;
    }
}
