import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
    selector: 'polp-bs-page-size-options-widget',
    templateUrl: './page-size-options-widget.component.html',
    styleUrls: ['./page-size-options-widget.component.css']
})
export class PageSizeOptionsWidgetComponent implements OnInit {

    @Input() initSize = 40;
    @Input() options: number[] = [40, 100, 200, 400, 800];

    @Output() onChange = new EventEmitter<number>();

    size: number = 40;

    constructor() { }

    ngOnInit(): void {
        this.size = this.initSize;
    }

    changeSize(s: number) {
        this.size = s;
        this.onChange.emit(s);
    }

}
