import { Component, HostListener, Input, OnDestroy, OnInit, AfterViewInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { faEye, faEyeSlash } from '@fortawesome/free-solid-svg-icons';
import { DefaultFormBaseComponent } from '@polpware/ngx-form-common';
import { Subscription } from 'rxjs';
import { v4 } from 'uuid';
import { IInputFieldSpec, makeValidations } from '../../utils/form-helper';

enum KEY_CODE {
    ENTER = 13
}

interface IInputFieldSpecExt extends IInputFieldSpec {
    fieldId: string;
    autocompleteKey?: string;
    visible: boolean;
}

function uuidv4() {
    return 'alpha-' + v4();
}

let formId = 1;

@Component({
    selector: 'polp-bs-prompt-form',
    templateUrl: './prompt-form.component.html',
    styleUrls: ['./prompt-form.component.css'],
    inputs: [
        'hideCancelBtn',
        'hideSubmitBtn',
        'submitBtnTxt',
        'cancelBtnTxt'
    ]
})
export class PromptFormComponent extends DefaultFormBaseComponent implements OnInit, OnDestroy, AfterViewInit {

    @Input() autocomplete: string;
    @Input() enableEnter: boolean = false;

    @Input() cancelBtnClasses: string;
    @Input() submitBtnClasses: string;

    @Input() fields: Array<IInputFieldSpec>;

    // Whether or not emit the init value after the form is built.
    @Input() emitInitValue: boolean = false;

    extFields: IInputFieldSpecExt[];

    form: FormGroup;

    faEyeSlash = faEyeSlash;
    faEye = faEye;

    private _subr: Subscription;

    readonly name: string;

    constructor(private readonly _builder: FormBuilder) {
        super();

        this.name = `promptName${formId++}`;
    }

    ngOnInit() {

        this.submitBtnTxt = this.submitBtnTxt || 'Confirm';
        this.cancelBtnTxt = this.cancelBtnTxt || 'Cancel';

        this.submitBtnClasses = this.submitBtnClasses || 'btn-primary';
        this.cancelBtnClasses = this.cancelBtnClasses || 'btn-secondary';

        if (this.autocomplete) {
            this.extFields = this.fields.map(a => {
                const k = `section-polp-bs-prompt-form-${this.autocomplete} ${a.name}`;
                return { ...a, fieldId: uuidv4(), autocompleteKey: k, visible: false };
            });
        } else {
            this.extFields = this.fields.map(a => {
                return { ...a, fieldId: uuidv4(), visible: false };
            });
        }

        const a = {};
        this.extFields.forEach(x => {
            a[x.name] = new FormControl(x.value, makeValidations(x.validators));
        });

        this.form = this._builder.group(a);

        this._subr = this.form.valueChanges.subscribe(a => {
            this.notifyValidation();
            this.notifyValueChanges(a);
        });
    }

    ngOnDestroy() {
        this._subr.unsubscribe();
    }

    ngAfterViewInit() {
        if (this.emitInitValue) {
            setTimeout(() => {
                const v = this.form.value;
                this.notifyValidation();
                this.notifyValueChanges(v);
            });
        }
    }

    cancel() {
        this.onCancel.emit(null);
    }

    submit() {
        this.onSave.emit(this.form.value);
    }

    @HostListener('window:keyup', ['$event'])
    keyEvent(event: KeyboardEvent) {
        if (this.enableEnter && event.keyCode === KEY_CODE.ENTER) {
            if (this.form.valid) {
                this.submit();
            }
        }
    }
}
