import { Input } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';

/**
 * Provides a base for most modal components.  This base is built with
 * the capability of synchronousely waiting for a modal to close.
 */
export abstract class ObservableModalAbstractComponent<TInput, TOutput> {

    @Input() modalId: number;

    result: Subject<TOutput> = new Subject();

    public abstract bsModalRef: BsModalRef;
    protected abstract bsModalService: BsModalService;

    closeModal(value: TOutput) {
        this.result.next(value);
        this.result.complete();

        if (this.modalId) {
            this.bsModalService.hide(this.modalId);
        } else {
            this.bsModalRef.hide();
        }
    }
}
