import { Component, Input, OnInit, Output } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';
import { IInputFieldSpec } from '../../utils/form-helper';

export interface IPromptFormInputs {
    autocomplete?: string;
    enableEnter?: boolean;

    hideCancelBtn?: boolean;
    hideCloseBtn?: boolean;
    hideConfirmBtn?: boolean;

    cancelBtnLabel?: string;
    confirmBtnLabel?: string;
    cancelBtnClasses?: string;
    confirmBtnClasses?: string;

    title: string;
    innerBody: string;
    fields: Array<IInputFieldSpec>;
}

@Component({
    selector: 'polp-bs-prompt-modal',
    templateUrl: './prompt-modal.component.html',
    styleUrls: ['./prompt-modal.component.css']
})
export class PromptModalComponent implements OnInit {

    @Input() autocomplete: string;
    @Input() enableEnter: boolean = false;

    @Input() hideCancelBtn: boolean = false;
    @Input() hideConfirmBtn: boolean = false;

    @Input() hideCloseBtn: boolean = false;

    @Input() cancelBtnLabel: string;
    @Input() confirmBtnLabel: string;
    @Input() cancelBtnClasses: string;
    @Input() confirmBtnClasses: string;

    @Input() title: string;
    @Input() innerBody: string;
    @Input() fields: Array<IInputFieldSpec>;

    @Output() result: Subject<{ [key: string]: any }>;

    constructor(
        private readonly _bsModalRef: BsModalRef) {
        this.result = new Subject();
    }

    ngOnInit(): void {
    }

    close() {
        this.result.next(null);
        this.result.complete();
        this._bsModalRef.hide();
    }

    confirm(value: { [key: string]: any }) {
        this.result.next(value);
        this.result.complete();
        this._bsModalRef.hide();
    }
}
