import { ValidatorFn, Validators } from '@angular/forms';
import { buildUrlValidator } from '@polpware/ngx-input-validators';

export type IValidationsSpec = {
    [key: string]: {
        value?: any;
        options?: any;
        message: string;
    }
};

export interface IInputFieldSpec {
    name: string;
    label: string;
    value: any;
    category: string;
    validators: IValidationsSpec;
    maskSettings?: {
        mask: string;
    }
}

export function makeValidations(data: IValidationsSpec) {
    const validations = Object.keys(data);
    let x = validations.map(a => {
        switch (a) {
            case 'required':
                return Validators.required;
            case 'min':
                return Validators.min(data.min.value);
            case 'max':
                return Validators.max(data.max.value);
            case 'pattern':
                return Validators.pattern(data.pattern.value);
            case 'minLength':
                return Validators.minLength(data.minLength.value);
            case 'maxLength':
                return Validators.maxLength(data.maxLength.value);
            case 'url':
                return buildUrlValidator(data.url.options);
            default:
                return null;
        }
    });

    x = x.filter(a => !!a);

    return Validators.compose(x as ValidatorFn[]);
}

